import React from "react";
import { Router } from "@reach/router";

import Layout from "../components/layout";
import SEO from "../components/seo";
import DefaultPage from "../components/pages/default";
import NovelDetailsPage from "../components/pages/novel-details";

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <Router prefix="personal-site">
      {/* <Profile path="/profile" />
      <Details path="/details" />
      <Login path="/login" /> */}
      <NovelDetailsPage path="/app/novel-details/:novelId" />
      <DefaultPage path="/app" />
    </Router>
  </Layout>
);

export default IndexPage;
